import UpdatePassword from '@/views/update-password'
import pay from '@/views/pay'
import register from '@/views/register'
import center from '@/views/center'
    import fangwuleixing from '@/views/modules/fangwuleixing/list'
    import news from '@/views/modules/news/list'
    import aboutus from '@/views/modules/aboutus/list'
    import lianxijilu from '@/views/modules/lianxijilu/list'
    import fangwuzulin from '@/views/modules/fangwuzulin/list'
    import kanfangjilu from '@/views/modules/kanfangjilu/list'
    import tuifangjilu from '@/views/modules/tuifangjilu/list'
    import zulinhetong from '@/views/modules/zulinhetong/list'
    import systemintro from '@/views/modules/systemintro/list'
    import yonghu from '@/views/modules/yonghu/list'
    import zufangjilu from '@/views/modules/zufangjilu/list'
    import fangdong from '@/views/modules/fangdong/list'
    import config from '@/views/modules/config/list'
    import newstype from '@/views/modules/newstype/list'


//2.   
export const routes = [{
    path: '/',
    name: '',
    component: Index,
    children: [{
      // main
      path: '/',
      name: '',
      component: Home,
      meta: {icon:'', title:'center', affix: true}
    }, {
      path: '/updatePassword',
      name: '',
      component: UpdatePassword,
      meta: {icon:'', title:'updatePassword'}
    }, {
      path: '/pay',
      name: '',
      component: pay,
      meta: {icon:'', title:'pay'}
    }, {
      path: '/center',
      name: '',
      component: center,
      meta: {icon:'', title:'center'}
    }
      ,{
	path: '/fangwuleixing',
        name: '',
        component: fangwuleixing
      }
      ,{
	path: '/news',
        name: '',
        component: news
      }
      ,{
	path: '/aboutus',
        name: '',
        component: aboutus
      }
      ,{
	path: '/lianxijilu',
        name: '',
        component: lianxijilu
      }
      ,{
	path: '/fangwuzulin',
        name: '',
        component: fangwuzulin
      }
      ,{
	path: '/kanfangjilu',
        name: '',
        component: kanfangjilu
      }
      ,{
	path: '/tuifangjilu',
        name: '',
        component: tuifangjilu
      }
      ,{
	path: '/zulinhetong',
        name: '',
        component: zulinhetong
      }
      ,{
	path: '/systemintro',
        name: '',
        component: systemintro
      }
      ,{
	path: '/yonghu',
        name: '',
        component: yonghu
      }
      ,{
	path: '/zufangjilu',
        name: '',
        component: zufangjilu
      }
      ,{
	path: '/fangdong',
        name: '',
        component: fangdong
      }
      ,{
	path: '/config',
        name: '',
        component: config
      }
      ,{
	path: '/newstype',
        name: '',
        component: newstype
      }
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {icon:'', title:'login'}
  },
  {
    path: '/register',
    name: 'register',
    component: register,
    meta: {icon:'', title:'register'}
  },
  {
    path: '*',
    component: NotFound
  }
]
//3.VueRouter  
const router = new VueRouter({
  mode: 'hash',
  /*hashhistory*/
  routes //  routes: routes
})
const originalPush = VueRouter.prototype.push
//push
VueRouter.prototype.push = function push(location) {
   return originalPush.call(this, location).catch(err => err)
}
export default router;
